import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { GoodsReceiptReportComponent } from './goods-receipt-report.component';

const routes: Routes = [
    {
        path: '', component: GoodsReceiptReportComponent
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class GoodsReceiptReportRoutingModule { }



