import { NgModule,NO_ERRORS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { GoodsReceiptReportRoutingModule } from './goods-receipt-report-routing.module';
import { GoodsReceiptReportComponent } from './goods-receipt-report.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    GoodsReceiptReportRoutingModule
  ],
  declarations: [
    GoodsReceiptReportComponent
  ],
  schemas:[
    NO_ERRORS_SCHEMA
  ]
})
export class GoodsReceiptReportModule { }



