import { Component, OnInit, ViewChild, ElementRef, ViewContainerRef, ComponentFactoryResolver, Inject } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators } from '@angular/forms';
import { routerTransition } from '../../../router.animations';
import { QuickaccessComponent, AllReportService, FileExportService, ItemMasterService } from '../../../shared';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';

@Component({
  selector: 'app-goods-receipt-report',
  templateUrl: './goods-receipt-report.component.html',
  styleUrls: ['./goods-receipt-report.component.scss']
})
export class GoodsReceiptReportComponent implements OnInit {

  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('storeComboBox') storeComboBox: jqxComboBoxComponent;
  @ViewChild('vendorComboBox') vendorComboBox: jqxComboBoxComponent;
  @ViewChild('typeComboBox') typeComboBox: jqxComboBoxComponent;
  sentItemFocus: any;
  sentItemAdapter: any = [];

  goodsReceiptReportForm: FormGroup;
  store_data: any;
  storeAdapter: any;
  vendor_data: any;
  vendorAdapter: any;
  apiUrl: string;
  typeAdapter: any[] = [
    { value: 'HO', label: 'Head Office' },
    { value: 'SES', label: 'Sister Estate' },
    { value: 'RET', label: 'Return' },
    { value: 'ADJ', label: 'Adjustment' },
    { value: 'VEN', label: 'Vendor' },
  ];

  store: boolean = false;

  constructor(
    private fb: FormBuilder,
    @Inject('API_URL') apiUrl: string,
    private rs: AllReportService,
    private fes: FileExportService,
    private ims: ItemMasterService,
    private componentFactoryResolver: ComponentFactoryResolver,
  ) {
    this.apiUrl = apiUrl;
    this.createForm();
  }

  ngOnInit() {
    this.createQuickAccess();
    this.store_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' }
        ],
        async: false,
        url: this.apiUrl + 'store?limit=all',
      };
    this.storeAdapter = new jqx.dataAdapter(this.store_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
    this.vendor_data =
      {
        datatype: "json",
        contentType: "application/json; charset=utf-8",
        datafields: [
          { name: 'id' },
          { name: 'name' }
        ],
        async: false,
        url: this.apiUrl + 'vendor?limit=all',
      };
    this.vendorAdapter = new jqx.dataAdapter(this.vendor_data, {
      downloadComplete: (data, status, xhr) => {
        let tData = data.data;
        return tData;
      }
    });
  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }
  createForm() {
    this.goodsReceiptReportForm = this.fb.group({
      'date_from': [null, Validators.required],
      'date_to': [null, Validators.required],
      'item_id': [null],
      'item_name': [null],
      'received_by': [null]
    });
  }

  saveBtn(post) {
    post['limit'] = 'all';
    post['grn_details'] = true;
    post['store_id'] = this.storeComboBox.val();
    post['vendor_id'] = this.vendorComboBox.val();
    //post['type'] = this.typeComboBox.val();
    post['report_type'] = 'goods_receipt_report';
    this.jqxLoader.open();
    this.rs.getGoodsReceiptReport(post).subscribe(result => {
      this.fes.SaveFile(result, 'Goods Receipt Report');
      this.jqxLoader.close();
    },
      error => {
        this.jqxLoader.close();
      });
  }

  sentItemFilter(searchPr, index) {
    let keycode = searchPr['keyCode'];
    if ((keycode == 40)) {
      document.getElementById('itemCode2').focus();
    }
    let searchString = searchPr['target'].value;
    let len = searchString.length;
    let dataString = searchString.substr(len - 1, len);
    let temp = searchString.replace(' ', '');
    temp = temp.replace(/\s/g, '');
    if (dataString == ' ' && temp.length > 1) {
      if (temp) {
        this.sentItemFocus = true;
        this.ims.index({ code: temp, transact: 1, limit: 'all' }).subscribe(
          response => {
            this.sentItemAdapter = [];
            this.sentItemAdapter = response;
          },
          error => {
            console.log(error);
          }
        );

      } else {
        this.sentItemFocus = false;
      }
    }
  }

  sentItemListSelected(selectedEvent) {
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      this.sentItemFocus = false;
      this.goodsReceiptReportForm.get('item_id').patchValue(selectedEvent.target.value);
      this.goodsReceiptReportForm.get('item_name').patchValue(selectedEvent.target.selectedOptions[0].text);
    }
  }

  storeChanged() {
    this.store = this.storeComboBox.val() ? true : false;
  }

}



